import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			imgUrl: [
				"/static/images/vipBanner_03.png",
				"/static/images/vipBanner_03.png",
				"/static/images/vipBanner_03.png"
			],
			shop_name:'',
			shop_address:'',
			shop_phone:'',
			goods:[],
			QRcodeUrl:'123',
			$api: '',
			qr:'',
			
		};
	},
	onShow() {
		this.getUserInfo();
		// this.showShopQecode();
	},
	onLoad(options) {
		this.shop_id = options.id
	},
	methods: {
		//获取信息
		getUserInfo() {
			let that = this;
			request({
					url:"index.php?s=/wap/shop/apiIndex",
					method:"POST",
					data: {
						shop_id: this.shop_id,
					}
				}).then(function(res){
					console.dir(res);
					that.shop_name = res.data.shop_info.shop_name;
					that.shop_address = res.data.shop_info.shop_address;
					that.shop_phone = res.data.shop_info.shop_phone;
					that.imgUrl = [
						that.baseURL + res.data.shop_info.shop_logo,
						that.baseURL + res.data.shop_info.shop_banner,
						that.baseURL + res.data.shop_info.shop_avatar,
					];
					that.goods = res.data.goods;
					that.qr = res.data.qr;
				});
		},
		// 充值会员
		goMoney() {
			uni.navigateTo({
				url: '/pages/components/money/money'
			})
		},
		// 咨询买单
		goRefer() {
			console.log(111);
			uni.navigateTo({
				url: '/pages/components/refer-member/refer-member'
			})
		},
	}
}
